// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
//import router from './router'
import VueRouter from 'vue-router';
Vue.config.productionTip = false

const index=resolve=>require(['./components/index/index.vue'],resolve);
const content=resolve=>require(['./components/content/content.vue'],resolve);
const sale=resolve=>require(['./components/sale/sale.vue'],resolve);
Vue.use(VueRouter);

const routes = [
  {path:"/", component: index},
  {path:"/sale", component: content},
  {path:"/buy", component: content}
]
 

const router = new VueRouter({
  routes 
})

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



